
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from "../../../api/index";
import axios from "axios";
export default {








directives: {
TransferDom
},
components: {
XHeader,
Actionsheet,
Group,
XInput,
Datetime,
XTextarea,
XButton
},
activated() {
this.helpObject = "";
this.helpDate = "";
this.helpMessage = "";

this.filesArr = [];
this.selectFile = [];
this.viewArr = [];






this.showLoading = false;
this.isClicked = false;
this.hasImg = 0;

this.$el.querySelector("#uploaderCustomFiles").innerHTML = "";
},
data() {
return {
menus: {
menuSelect: "拍照",
menuCapture: "从相册选取"
},
showMenus: false,
helpObject: "",
helpDate: "",
helpMessage: "",
showLoading: false,
isClicked: false,
maxSize: 10240000,
maxCount: 9,
filesArr: [],
selectFile: [],
viewArr: [],
hasImg: 0,
percent: 0
};
},
mounted() {
this.bindEvent();
this.delImg();
},
watch: {
helpObject: function(value) {
if (value && this.helpDate && this.helpMessage) {
this.isClicked = true;
}
},
helpDate: function(value) {
if (value && this.helpObject && this.helpMessage) {
this.isClicked = true;
}
},
helpMessage: function(value) {
if (value && this.helpDate && this.helpObject) {
this.isClicked = true;
}
},
filesArr: function(value) {
if (value.length >= this.maxCount) {
let uploader = document.querySelector(".weui-uploader__input-box");
uploader.style.display = "none";
} else {
let uploader = document.querySelector(".weui-uploader__input-box");
uploader.style.display = "block";
}
}
},
methods: {
bindEvent() {
let self = this;
let uploader = self.$el.querySelector("#uploaderCustomInput");
let uploaderCustomeFiles = self.$el.querySelector("#uploaderCustomFiles");

uploader.addEventListener("change", function(event) {
let files = event.target.files || event.dataTransfer.files;

if (files.length === 0) {
return;
}
if (files.length >= self.maxCount) {
self.$vux.toast.text(`最多上传${self.maxCount}张`, "middle");
return;
}

for (var i = 0; i < files.length; i++) {
let file = files[i];

if (file.size >= self.maxSize) {
self.$vux.toast.text(`最大不能超过10M`, "middle");
return;
}

self.selectFile.push(0);
self.readFile(file);




















































uploader.value = "";
}
});
},
readFile(file) {
let self = this;
let reader = new FileReader();
reader.onprogress = function(e) {
let uploader_per = self.$refs.fileWrapper.querySelector(
`#prview${self.hasImg} .percent`
);
let percent = Math.round(e.loaded / e.total * 100);

self.percent = percent;
if (percent < 99) {
uploader_per.style.width = percent + "%";

}
};
reader.onload = function(e) {
let uploader_file = self.$refs.fileWrapper.querySelector(
`#prview${self.hasImg}`
);
let uploader_per = self.$refs.fileWrapper.querySelector(
`#prview${self.hasImg} .percent`
);

var src = e.target.result;
self.viewArr.push(src);

let data;
var img = new Image();
img.src = src;

img.onload = function() {
if (src.length <= 100 * 1024) {
data = src;
} else {
data = self.compress(img);
}


self.filesArr.push(self.dataURItoBlob(data));
};
uploader_per.style.width = "100%";


let $delBtn = '<div class="weui_del"><i></i></div>';
let delBtn = self.parseToDOM($delBtn)[0];

uploader_file.innerHTML = $delBtn;
self.hasImg++;
};
reader.onloadend = function(e) {
self.percent = 0;
};
reader.readAsDataURL(file);
},
dataURItoBlob(dataUrl) {
var byteString = atob(dataUrl.split(",")[1]);
var mimeString = dataUrl
.split(",")[0]
.split(":")[1]
.split(";")[0];
var ab = new ArrayBuffer(byteString.length);
var ia = new Uint8Array(ab);
for (var i = 0; i < byteString.length; i++) {
ia[i] = byteString.charCodeAt(i);
}
return new Blob([ab], { type: mimeString });
},
parseToDOM(str) {
var ul = document.createElement("ul");
if (typeof str == "string") ul.innerHTML = str;
return ul.childNodes;
},
compress(img) {
let canvas = document.createElement("canvas");

let context = canvas.getContext("2d");



var originWidth = img.width;
var originHeight = img.height;
let initSize = img.src.length;

var maxWidth = 800,
maxHeight = 800;

var targetWidth = originWidth,
targetHeight = originHeight;

if (originWidth > maxWidth || originHeight > maxHeight) {
if (originWidth / originHeight > maxWidth / maxHeight) {

targetWidth = maxWidth;
targetHeight = Math.round(maxWidth * (originHeight / originWidth));
} else {
targetHeight = maxHeight;
targetWidth = Math.round(maxHeight * (originWidth / originHeight));
}
}


canvas.width = targetWidth;
canvas.height = targetHeight;

context.clearRect(0, 0, targetWidth, targetHeight);

context.drawImage(img, 0, 0, targetWidth, targetHeight);



let ndata = canvas.toDataURL("image/jpeg", 0.4);
console.log("压缩前：" + initSize);
console.log("压缩后：" + ndata.length);
console.log(
"压缩率：" + ~~(100 * (initSize - ndata.length) / initSize) + "%"
);

return ndata;
},

delImg() {
let self = this;
let uploaderCustomeFiles = self.$el.querySelector("#uploaderCustomFiles");

uploaderCustomeFiles.addEventListener("click", function(event) {
let currentNode = event.target.parentNode.parentNode;

let index = [...uploaderCustomeFiles.childNodes].indexOf(currentNode);

self.selectFile.splice(index, 1);
self.viewArr.splice(index, 1);
self.filesArr.splice(index, 1);




self.hasImg--;
});
},
submit() {
const _this = this;

let config = {
headers: {
"Content-Type": "multipart/form-data"
}
};
_this.showLoading = true;
let userid = window.localStorage.getItem("user");

let uploadData = new FormData();
_this.filesArr.map(file => {
uploadData.append("image[]", file);
});
uploadData.append("visitor_id", userid);
uploadData.append("objector", this.helpObject);
uploadData.append("visit_time", this.helpDate);
uploadData.append("content_feedback", this.helpMessage);

let ax = axios.create();

ax.post("/zbf/help/add", uploadData, config).then(res => {
if (res.data.code === 1) {
_this.showLoading = false;

_this.$router.replace("/home/help?status=dynamic");
this.$store.dispatch("getHelpsList");
} else {
this.$vux.toast.text(res.data.msg, "middle");
_this.showLoading = false;
}
});
}
}
};



